.search__input {
  height: 22px;
  width: 65%;
  color:  #1A1B22;
  font-family: 'Roboto', Arial, sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 22px;
  text-align: left;
  border: none;
  outline: none;
  margin-top: 21px;
}

.search__input:hover{
  box-shadow: 0 0 10px rgba(0, 0, 0, .3);
}

.search__input:focus {
  background-color: rgba(47, 113, 229, .1);
}

.search__input::-webkit-input-placeholder {
  color: #B6BCBF;
  text-indent: 0;
  transition: text-indent .3s ease;
}
.search__input::-moz-placeholder {
  color: #B6BCBF;
  text-indent: 0;
  transition: text-indent .3s ease;
}

.search__input:-moz-placeholder {
  color: #B6BCBF;
  text-indent: 0;
  transition: text-indent .3s ease;
}

.search__input:-ms-input-placeholder  {
  color: #B6BCBF;
  text-indent: 0;
  transition: text-indent .3s ease;
}

.search__input:focus::-webkit-input-placeholder {
  text-indent: 500px;
  transition: text-indent .3s ease;
}

.search__input:focus::-moz-placeholder{
  text-indent: 500px;
  transition: text-indent .3s ease;
}

.search__input:focus:-moz-placeholder  {
  text-indent: 500px;
  transition: text-indent .3s ease;
}

.search__input:-ms-input-placeholder {
  text-indent: 500px;
  transition: text-indent .3s ease;
}


@media (max-width: 768px) {
  .search__input {
    margin-top: 15px;
  }
}

@media (max-width: 500px) {
  .search__input  {
    width: calc(100% - 32px);
    border-radius: 100px;
    border: 1px solid #d1d2d6;
    height: 56px;
    padding: 0 14px;
  }
}



